@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: 400;
  src: url("../resources/fonts/GothamPro-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: 700;
  src: url("../resources/fonts/GothamPro-Bold.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
  background: #313131;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  max-width: 390px;
  width: 100%;
  margin: 0 auto;
  font-family: "Gotham Pro", sans-serif;
  font-size: 12px;
  background: #fff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  max-width: 100%;
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

.container-fluid {
  max-width: 100%;
  margin: 0 auto;
}

:root {
  --font-family: "Roboto", sans-serif;
  --content-width: 1230px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-primary: #e3e3e3;
  --grid-gap: 30px;
}

.custom-select {
  position: relative;
  width: 170px;
  height: 50px;
  border-radius: 15px;
  background: #fff;
}
.custom-select.is-active .custom-select__body {
  display: block;
}
.custom-select__header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  padding: 14px 9px 0 0;
  cursor: pointer;
}
.custom-select__icon {
  width: 18px;
  height: 7px;
  margin-left: auto;
}
.custom-select__current {
  margin-left: auto;
  font-size: 22px;
  font-weight: 700;
  line-height: 21px;
  text-align: center;
  color: #2C2B31;
  text-transform: uppercase;
}
.custom-select__body {
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  right: 0;
  display: none;
  border-radius: 15px;
  background: #fff;
  overflow: hidden;
}
.custom-select__item {
  padding: 10px 0;
  font-size: 22px;
  font-weight: 700;
  line-height: 21px;
  text-align: center;
  color: #2C2B31;
  text-transform: uppercase;
  transition: background 0.3s ease-in-out;
  cursor: pointer;
}
.custom-select__item:hover {
  background: #a0a0a0;
}

.header-top {
  height: 33px;
  background: #F19D2F;
}
.header-top__special-offer {
  padding-top: 11px;
  font-size: 13px;
  font-weight: 400;
  line-height: 12px;
  color: #2C2B31;
  text-align: center;
  text-transform: uppercase;
}
.header-logo {
  padding: 19px 0;
  background: #FFD1AC;
  text-align: center;
}
.header-logo__icon {
  margin-bottom: 10px;
}
.header-logo__descr {
  font-size: 13px;
  font-weight: 400;
  line-height: 13px;
  text-align: center;
}

.hero {
  position: relative;
  height: 280px;
  background-image: url("../resources/images/hero-bg.png");
  background-repeat: no-repeat;
}
.hero-item {
  position: absolute;
  bottom: 14px;
  padding: 9px 7px;
  border-radius: 7px;
}
.hero-time {
  left: 13px;
  background: #F19D2F;
}
.hero-time__offer {
  margin-bottom: 10px;
  font-size: 11px;
  font-weight: 700;
  line-height: 10px;
  color: #fff;
  text-transform: uppercase;
}
.hero-time__items {
  display: flex;
  font-size: 21px;
  font-weight: 700;
  line-height: 20px;
  color: #fff;
}
.hero-time__item {
  position: relative;
}
.hero-time__item:not(:last-child) {
  margin-right: 6px;
}
.hero-time__item:not(:last-child)::after {
  content: ":";
  position: absolute;
}
.hero-price {
  right: 13px;
  text-align: center;
  background: #37D997;
}
.hero-price__old {
  position: relative;
  display: inline-block;
  margin-bottom: 8px;
  font-size: 18px;
  font-weight: 700;
  line-height: 17px;
  text-align: center;
  color: #fff;
}
.hero-price__old::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 3px;
  background: #E27500;
  border-radius: 3px;
  transform: translateY(-50%);
}
.hero-price__now {
  font-size: 22px;
  font-weight: 700;
  line-height: 21px;
  color: #fff;
}

.card {
  padding: 11px 0 40px;
  background: #F0F0F0;
}
.card-stats {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 6px auto 20px;
}
.card-stats__raiting {
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  color: #2C2B31;
}
.card-stats__best-selling {
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  color: #37D997;
  text-decoration: underline;
}
.card-title {
  margin-bottom: 20px;
  font-size: 19px;
  font-weight: 700;
  line-height: 18px;
  color: #2C2B31;
}
.card-swiper {
  width: 100%;
  margin: 0 auto 20px;
  text-align: center;
}
.card-swiper .swiper-slide {
  cursor: pointer;
}
.card-swiper-thumbs {
  width: 100%;
  margin-bottom: 20px;
}
.card-swiper-thumbs .swiper-slide-thumb-active {
  opacity: 0.4;
}
.card-swiper-thumbs .swiper-slide {
  cursor: pointer;
}
.card-info__delivery {
  margin-bottom: 12px;
  font-size: 15px;
  font-weight: 400;
  line-height: 15px;
  color: #000;
}
.card-info__stock {
  margin-bottom: 12px;
  font-size: 15px;
  font-weight: 400;
  line-height: 15px;
  color: #000;
}
.card-info__stars {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.card-info__star {
  margin-right: 4px;
  width: 14px;
  height: 13px;
}
.card-info__star:last-child {
  margin-right: 10px;
}
.card-info__star_visible {
  background-image: url("../resources/images/star.svg");
  background-size: contain;
  background-repeat: no-repeat;
}
.card-info__feedback-count {
  font-size: 15px;
  font-weight: 400;
  line-height: 15px;
  color: #2C2B31;
}
.card-form-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.card-form-options__item {
  text-align: center;
}
.card-form-options__heading {
  margin-bottom: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 10px;
  color: #2C2B31;
}
.card-form__btn {
  display: block;
  width: 100%;
  height: 50px;
  padding-top: 11px;
  border-radius: 15px;
  background: #37D997;
  font-size: 30px;
  font-weight: 400;
  line-height: 29px;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
}